	module.exports = {"data":{"post":{"html":"<p>We've just recovered from a massive network outage at blogg.de (as well as the other sites hosted by nu2m.de). We were down for about 6 hours. All interfaces on our router would freeze immediately upon restart, and it took me a while to figure out the cause (it wasn't the router, so I wasted a couple of hours working on that). Everything now seems to be back up. The hard part will be figuring out why it happened and keeping it from happening again.  </p>\n<p>I'm the sole admin in Hamburg, and it's been a long, lonely evening.</p>","fields":{"title":"Outage at blogg.de","date":"23 Dec 2005","url":"https://papascott.de/2005-12-23-outage-at-bloggde/"}}},"pathContext":{"slug":"/2005-12-23-outage-at-bloggde/","prev":{"fields":{"title":"Eggnog in Quantity","date":"23 Dec 2005","slug":"/2005-12-23-eggnog-in-quantity/"}},"next":{"fields":{"title":"Christmas Traditions","date":"25 Dec 2005","slug":"/2005-12-25-christmas-traditions/"}}}}

